import {
    Button,
    Card,
    CardBody,
    CardHeader,
    CardTitle,
    Tab,
    Tabs,
  } from "react-bootstrap";
  import { baseUrl } from "../../../../../utils";
  import React, { Suspense, useState } from "react";
  import NoData from "../../layout/common/NoData";
  import { NoCameraImgPost } from "../../../../../actions/api";
  import ConnectButton from "../../layout/common/connectButton";
  import Content from "../../layout/common/content";
  import AuthorInfo from "./authorInfo";
  import PostMedia from "../../layout/common/postMedia";
  import SeeMoreModal from "../../layout/common/SeeMoreModal";
  import PostPopup from "../../layout/popup/postPopup";
  import { getwallRecordSentimentByPostStatisticsNagativeMore, getwallRecordSentimentByPostStatisticsPositiveMore } from "../../../../../actions/analyticsNew";
  import PostdetailPopup from "../../layout/popup/postdetailPopup";
  const SentimentpostSkeleton = React.lazy(() =>
    import("../../layout/skeleton/SentimentpostSkeleton")
  );
  
  const PostData = (e) => {
    return (
      <div className="px-0 py-3 cursor-pointer border-bottom">
        <div className="d-flex align-items-center flex-md-row flex-column">
          <div onClick={() => { e.setpostInfo(e); e.handlePostpopup(true) }} className="symbol_item position-relative">
            <div className="symbol symbol-100 symbol-2by2 flex-shrink-0 me-4">
              <Button
                type="button"
                className="border-0 p-0 bg-transparent rounded"
              >
                <span
                  className="symbol-label no-repeat-fit-background rounded"
                  style={{
                    backgroundImage: e.image
                      ? e.image
                      : NoCameraImgPost,
                  }}
                >
                  <PostMedia
                    postFile={e.image}
                    postId={"postPrimaryId"}
                    className={"img-fluid"}
                  />
                </span>
              </Button>
            </div>
          </div>
          <div className="w-100 d-flex">
            <div className="w-100 me-10">
              <div className="d-flex flex-grow-1 w-100 mb-2 justify-content-between pe-10">
                <AuthorInfo
                  picture={e.authorImage}
                  username={e.authorUsername}
                  date={e.created}
                />
                <div class="list_category_icon cursor-pointer pt-1">
                  <i className={`fab ${e.network.icon} font-size-h2`}></i>
                </div>
              </div>
              <div className="text-dark font-weight-normal font-size-sm overflow-hidden text-ellipsis line-clamp-3">
                <Content content={e.content} />
              </div>
            </div>
            <div className="conn_status_wrap">
              <ConnectButton link={e.link} />
            </div>
          </div>
        </div>
      </div>
    );
  };
  
  const SentimentPost = (props) => {
    const { loader } = props;
    const { negative, positive, negativeCount, positiveCount } =
      props && Object.keys(props).length ? props[1] : [];
  
    const [show, setShow] = useState(false);
    const [showPost, setshowPost] = useState(false);
    const [postInfo, setpostInfo] = useState(false);
    const [activeTab, setactiveTab] = useState("positive");
    const [postCount, setpostCount] = useState(positiveCount);
  
    const handlePopup = (event) => {
      setShow(event);
    };

    const handlePostpopup = (event) => {
        setshowPost(event);
      };

  
    const handleSelect = (e) => {
      setactiveTab(e)
      setpostCount(e=="positive" ? positiveCount : negativeCount)
    }
  
    return (
      <>
        <Card className="sw-card-custom gutter-b p-5 card-shadow">
          <CardHeader className="p-0 border-0">
            <CardTitle className="mb-3 d-flex align-items-center justify-content-between">
              <h6 class="card-label font-weight-bolder">
                <img
                  src={baseUrl(`dashboard/analytic/ai.svg`)}
                  height={"20px"}
                  width={"20px"}
                />{" "}
                Sentiment
              </h6>
              { postCount>5 ?  <Button
                onClick={() => handlePopup(true)}
                variant="link"
                className="p-0"
              >  See More
              </Button> : null }
            </CardTitle>
          </CardHeader>
          <CardBody className="p-0">
            <Tabs
              activeKey={activeTab}
              onSelect={handleSelect}
              defaultActiveKey="positive"
              id="uncontrolled-tab-example"
              className="nav-tabs-line sw-custom-nav nav nav-tabs mb-3"
            >
              <Tab
                eventKey="positive"
                title={
                  <div className="px-2">
                    <img
                      src={baseUrl(`dashboard/analytic/smile-active.svg`)}
                      height={"16px"}
                      width={"16px"}
                    />{" "}
                    positive
                  </div>
                }
              >
                {loader ? (
                  <Suspense fallback={<></>}>
                    <SentimentpostSkeleton />
                  </Suspense>
                ) : positive && Object.keys(positive).length ? (
                  positive.map((data) => {
                    return <PostData setpostInfo={setpostInfo} handlePostpopup={handlePostpopup} {...data} />;
                  })
                ) : (
                  <NoData />
                )}
              </Tab>
              <Tab
                eventKey="negative"
                title={
                  <div className="px-2">
                    <img
                      src={baseUrl(`dashboard/analytic/sad.svg`)}
                      height={"16px"}
                      width={"16px"}
                    />{" "}
                    Negative
                  </div>
                }
              >
                {loader ? (
                  <Suspense fallback={<></>}>
                    <SentimentpostSkeleton />
                  </Suspense>
                ) : negative && Object.keys(negative).length ? (
                  negative.map((data) => {
                    return <PostData setpostInfo={setpostInfo} handlePostpopup={handlePostpopup} {...data} />;
                  })
                ) : (
                  <NoData />
                )}
              </Tab>
            </Tabs>
          </CardBody>
        </Card>
  
            <SeeMoreModal
                size={"lg"}
                showHide={show}
                handlePopup={handlePopup}
                title={ `Sentiment ${activeTab}`}>
                <Suspense fallback={<></>}>
                    <PostPopup moreData={activeTab=='positive' ? getwallRecordSentimentByPostStatisticsPositiveMore : getwallRecordSentimentByPostStatisticsNagativeMore={}} type={activeTab=="activeTab" ? 3 : 4} {...props} />
                </Suspense>
            </SeeMoreModal>


            <SeeMoreModal
                size={"lg"}
                showHide={showPost}
                handlePopup={handlePostpopup}
                title={ `General Information`}
                >
                <Suspense fallback={<></>}>
                    <PostdetailPopup  {...postInfo} />
                </Suspense>
            </SeeMoreModal>
      </>
    );
  };
  
  export default React.memo(SentimentPost);
  