import React, { useState } from "react";
import { getFirstLetterOfString, manageSpace } from "../../../../../utils";
import moment from "moment";

const AuthorInfo = (props) => {
  const [imageError, setImageError] = useState(true);

  return (
    <div className="d-flex align-items-center w-50">
      <div className="position-relative">
        <div className="symbol symbol-35 symbol-light me-3 con_user_img cursor-pointer rounded-circle overflow-hidden">
          {imageError ? (
            <div className="symbol-image">{getFirstLetterOfString(props.username)}</div>
          ) : (
            <img
              onError={() => setImageError(true)}
              src={props.picture}
              className="img-fluid"
              width={"20px"}
              height={"20px"}
            />
          )}
        </div>
        {props.isIcon ? (
          <div className="m_social_icon_new card-shadow position-absolute bg-white rounded-circle d-flex align-items-center justify-content-center">
            <i className={`fab ${props.isIcon}`}></i>
          </div>
        ) : null}
      </div>
      <div class="d-flex text-left flex-column conn_user_info cursor-pointer">
        <p class="text-dark mb-0 font-weight-boldest overflow-hidden text-nowrap text-ellipsis author_name text-capitalize">
          {props.username}
        </p>
        <p class="m-0">
          <small class="text-muted overflow-hidden text-nowrap text-ellipsis post_count me-2 ">
            {props.username ? `@${manageSpace(props.username)}` : null}
          </small>

         { props.date ? 
          <small class="text-muted overflow-hidden text-nowrap text-ellipsis followers_count ">
            {moment(new Date(props.date * 1000)).fromNow()}
          </small> : null }
        </p>
      </div>
    </div>
  );
};

export default React.memo(AuthorInfo);
