import React from "react";
import reactStringReplace from "react-string-replace";

const Content = (props) => {
  return reactStringReplace(props.content, /#(\w+)/g, (match, i) => (
    <div
      className="tb_text_hashtag"
      style={{
        background: "#AFE0FF",
        display: "inline-flex",
        borderRadius: 3,
        padding: 2,
        margin: 1,
      }}
    >
      @{match}
    </div>
  ));
};

export default React.memo(Content)
