import React, { Component } from "react";
import ConnectButton from "../common/connectButton";
import AuthorInfo from "../../components/content/authorInfo";

class PostdetailPopup extends Component {
  render() {
    const {
      content,
      link,
      created,
      authorUsername,
      authorImage,
      network,
      image,
      sentiment,
      followerCount,
      feedName,
      tags,
    } = this.props;

    console.log(this.props, "dsfsdfsdfsddsfsdf");

    return (
      <div class="influential_contributors_custom_modal px-5">
        <div className="influential-data-list sw_theme ">
          <div className="d-flex justify-content-between align-items-center">
            <div className="d-flex align-items-center">
              <AuthorInfo
                picture={authorImage}
                username={authorUsername}
                isIcon={network.icon}
                date={created}
              />
            </div>
          </div>

          <img src={image} className="img-fluid" />

          {content}

          <div className="conn_status connnected">
            <ConnectButton link={link} />
          </div>

          {tags && Object.keys(tags).length ? (
            <ul>
              {tags.map((data) => {
                return <li>{data}</li>;
              })}
            </ul>
          ) : null}

          {sentiment}
          {followerCount}
          {feedName}
        </div>
      </div>
    );
  }
}

export default PostdetailPopup;
