    import { Button } from "react-bootstrap";
    import Loader from "react-loader-spinner";
    import SVG from "react-inlinesvg";
    import React, { useEffect } from "react";
    import { toAbsoluteUrl } from "../../../utils";

    const PS = (props) => {
    const { isPdfDownloading, analyticsStatus, updateSearchText } = props;

    const onSearchText = () => {
        let textInput = document.getElementById("searchTextInfluencer");
        let timeout = null;
        textInput.addEventListener("input", (e) => {
        clearTimeout(timeout);
        timeout = setTimeout(() => updateSearchText(textInput.value), 1000);
        });
    };

    useEffect(() => {
        onSearchText()
    }, []);

    return (
        <div className="d-flex align-items-center justify-content-end mb-3">
        <Button
            variant="dark me-4 px-4 px-sm-5 export-pdf d-flex align-items-center"
            size="sm"
            className={
            isPdfDownloading
                ? `d-flex pdf-loading`
                : `d-flex ,${analyticsStatus?.trialAccessAPI ? `disabled` : ``}`
            }
            disabled={isPdfDownloading ? `disabled` : ""}
        >
            <span className="svg-icon svg-icon-sm">
            <SVG
                className="h-75 align-self-end"
                src={toAbsoluteUrl("/svg/export.svg")}
            ></SVG>
            </span>
            <span className="btn_txt">
            {isPdfDownloading ? (
                <Loader
                type="ThreeDots"
                color="#333333"
                height={15}
                width={25}
                style={{ marginRight: 5 }}
                />
            ) : (
                `Export`
            )}
            </span>
        </Button>

        <form onSubmit={(e) => e.preventDefault()}>
            <div class="form-group m-0">
            <div class="input-icon input-icon-right position-relative">
                <input
                class="form-control search-input"
                id="searchTextInfluencer"
                type="text"
                placeholder="Search Post..."
                />
                <span>
                <i class="fa fa-search icon-md text-muted"></i>
                </span>
            </div>
            </div>
        </form>
        </div>
    );
    };

    const PopupSearch = React.memo(PS);
    export default PopupSearch;
